import { closeAllPopups } from "./popup.js"

let debug = false
function log(...params) {
    if (debug) console.log(...params)
}
export async function loadCards(filter = null) {
    const cardList = document.querySelector(".popular__cards-list")
    if (!cardList) return
    cleanCards(cardList)
    showLoader(cardList)
    await sleep(2000)

    fetch("data/cards.json")
        .then((response) => response.json())
        .then(doLoadCards)
        .then(() => lazyLoadInstance.update())
    function doLoadCards(cards) {
        const filteredCards = filterCards(cards, filter)
        log("filtered cards", filteredCards)
        removeLoader(cardList)
        if (filteredCards.length === 0) {
            showEmptyCard(cardList)
        } else {
            for (const card of filteredCards) {
                const html = loadCard(card)
                cardList.insertAdjacentHTML("beforeend", html)
            }
        }
    }
}
function sleep(ms) {
    return new Promise((resolve) => setTimeout(resolve, ms))
}
function cleanCards(cardList) {
    while (cardList.children.length > 0) {
        cardList.children[cardList.children.length - 1].remove()
    }
}
const loader = '<li class="loader"><div></div><div></div><div></div><div></div></li>'
function showLoader(cardList) {
    cleanCards(cardList)
    cardList.classList.add("loading")
    cardList.insertAdjacentHTML("afterbegin", loader)
}
function removeLoader(cardList) {
    const loader = cardList.querySelector(".loader")
    if (loader) loader.remove()
    cardList.classList.remove("loading")
}
function filterCards(cards, filter) {
    if (!filter) return cards
    let result = []
    const requiredKeys = Object.entries(filter)
        .filter((entry) => entry[1].length > 0)
        .reduce((acc, curr) => acc.add(curr[0]), new Set())
    log("requiredKeys", requiredKeys)
    log("filter", filter)
    for (const card of cards) {
        let ok = 0
        log(" ------------")
        log("card", card)
        for (const key of Object.keys(filter)) {
            log("key", key)
            if (key === "price") {
                if (parseInt(card[key]) <= parseInt(filter[key][0])) {
                    ok += 1
                    log(key, card[key], "is ok")
                } else {
                    log(key, card[key], "is not ok")
                }
            } else if (key === "location") {
                if (filter[key].includes("all")) {
                    ok += 1
                    log(key, card[key], "is ok")
                } else if (filter[key].includes(card[key])) {
                    ok += 1
                    log(key, card[key], "is ok")
                } else {
                    log(key, card[key], "is not ok")
                }
            } else if (filter[key].includes(card[key])) {
                ok += 1
                log(card[key], key, "is ok")
            } else {
                log(key, card[key], "is not ok")
            }
        }
        log("requiredKeys", requiredKeys, ok)
        if (requiredKeys.size === ok) {
            result.push(card)
            log("card", card, "accepted")
        } else {
            log("card", card, "not accepted")
        }
    }
    return result
}
function showEmptyCard(cardList) {
    cardList.insertAdjacentHTML(
        "afterbegin",
        `
   <li class="popular__card card card--empty">
         <div class="card__data">
            <div class="card__header">
               <h6 class="card__title">Подходящих карточек не найдено</h6>
            </div>
         </div>
   </li>
   `
    )
}
function loadCard(card) {
    return `
<li class="popular__card card${card.isNew ? " card--new" : ""}">
      <div class="card__image">
         <img 
            src="img/spinner.svg" class="lazy"
            data-src="${card.img}" alt="${card.title}" />
      </div>
      <div class="card__data">
         <div class="card__header">
            <h6 class="card__title">${card.title}</h6>
            <span class="card__price"> $${card.price}${card.pricePer ? "/" + card.pricePer : ""
        }</span>
         </div>
         <ul class="card__properties">
            <li class="card__property">
                  <span>Тип:</span> ${card.type}
            </li>
            <li class="card__property"><span>Статус:</span> ${card.status}</li>
            <li class="card__property"><span>Район:</span> ${card.location}</li>
         </ul>
         <div class="card__buttons">
            <button type="button" class="button light-button">
                  Подробнее
            </button>
            <button type="button" class="button dark-button">
                  Оставить заявку
            </button>
         </div>
      </div>
</li>
`
}
export function getFilter(form) {
    const formData = new FormData(form)
    const names = new Set()
    const filter = {}
    form.querySelectorAll("[name]").forEach((name) => names.add(name.name))
    for (const name of names) {
        filter[name] = formData.getAll(name)
    }
    //  console.log("filter", filter)
    return filter
}
export function loadCardsListener() {
    const form = document.querySelector(".popular__filter form")
    if (!form) return
    form.addEventListener("submit", (e) => {
        e.preventDefault()
        const filter = getFilter(form)
        loadCards(filter)
        closeAllPopups()
    })
}
// import { loadCards, loadCardsListener } from "./modules/cards.js"
const cardList = document.querySelector(".popular__cards-list")
if (cardList) {
    loadCards()
}
loadCardsListener()
