import { initSearch } from "./search.js"
import { loadScript } from "./functions.js";

const fadeTimeout = 500
const fadeOutOnClose = false
let captureTab = true
let popupContent
let popupContentParent
let providedClassNames
const popupContainer = document.querySelector(".popup-outer-container")
const popupBody = popupContainer.querySelector(".popup-body")
function openSmallerPopup(selector, ...classNames) {
    loadScript('js/forms.min.js')
    popupContent = document.querySelector(selector)
    popupContentParent = popupContent.parentElement
    providedClassNames = classNames
    popupBody.insertAdjacentElement("afterbegin", popupContent)
    // popupContainer.classList.add("popup--open")
    popupContainer.classList.add("popup--open", ...classNames)
    document.body.classList.add("_lock")
    if (captureTab) window.addEventListener("keydown", lockTabInside)
}
function closeSmallerPopup() {
    if (captureTab) window.removeEventListener("keydown", lockTabInside)
    fadeOut(popupContainer)
    const smallerPopupIndex = providedClassNames.indexOf("smaller-popup")
    if (smallerPopupIndex > -1) providedClassNames.splice(smallerPopupIndex, 1)
    popupContainer.classList.remove(...providedClassNames)
    setTimeout(() => {
        if (smallerPopupIndex > -1) popupContainer.classList.remove("smaller-popup")
        popupContentParent.insertAdjacentElement("afterbegin", popupContent)
    }, fadeTimeout)
}
function fadeOut(el) {
    if (!el) return
    if (fadeOutOnClose) {
        if (el.classList.contains("popup--open")) {
            if (!el.classList.contains("popup--is-fading-out")) {
                el.classList.add("popup--is-fading-out")
                setTimeout(() => {
                    el.classList.remove("popup--is-fading-out")
                    el.classList.remove("popup--open")
                    document.body.classList.remove("_lock")
                }, fadeTimeout)
            }
        }
    } else {
        el.classList.remove("popup--open")
        document.body.classList.remove("_lock")
    }
}
export function closeAllPopups() {
    if (captureTab) window.removeEventListener("keydown", lockTabInside)
    document.querySelectorAll(".popup--open").forEach((popup) => closeSmallerPopup(popup))
}

export function initPopups() {
    document.addEventListener("click", (e) => {
        if (e.target.closest(".close-button") || e.target.closest('.burger__secondary-link')) {
            e.preventDefault()
            fadeOut(e.target.closest(".popup--open"))
            closeSmallerPopup()
        }
    })

    document.querySelectorAll(".plan-trip-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            loadScript('js/datepicker.min.js')
            openSmallerPopup(".plan-trip", "plan-trip-popup", "smaller-popup")
        })
    )
    document.querySelectorAll(".open-search-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            openSmallerPopup(".search", "search-popup")
            initSearch()
        })
    )
    document.querySelectorAll(".open-message-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            openSmallerPopup(".contacts-popup", "smaller-popup")
        })
    )
    document.querySelectorAll(".open-callback-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            openSmallerPopup(".callback-popup", "smaller-popup")
        })
    )
    document.querySelectorAll(".open-burger-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            openSmallerPopup(".burger", "burger-popup")
        })
    )
    document.querySelectorAll(".open-filter-button").forEach((btn) =>
        btn.addEventListener("click", (e) => {
            e.preventDefault()
            openSmallerPopup(".filter", "filter-popup")
        })
    )

    // close popups
    document.addEventListener("click", (e) => {
        // console.log(e.target)
        if (e.target.matches(".popup-container") || e.target.matches(".popup--open")) {
            closeAllPopups()
        }
    })
    //  document.addEventListener("keydown", (e) => {
    //      if (e.key === "Escape") {
    //          closeAllPopups()
    //      }
    //  })
}

function lockTabInside(e) {
    if (e.keyCode === 9) {
        let focusable = document
            .querySelector(".popup--open")
            .querySelectorAll("input,button,select,textarea")
        if (focusable.length) {
            let first = focusable[0]
            let last = focusable[focusable.length - 1]
            let shift = e.shiftKey
            if (shift) {
                if (e.target === first) {
                    // shift-tab pressed on first input in dialog
                    last.focus()
                    e.preventDefault()
                }
            } else {
                if (e.target === last) {
                    // tab pressed on last input in dialog
                    first.focus()
                    e.preventDefault()
                }
            }
        }
    }
}
